    .works {

        &__container {
            position: relative;
        }

        &__slide {
            &-sm {}

            &-xs {
                @include media('tablet-1000', true) {
                    display: none;
                }
            }
        }

        &__row {}

        &__title {
            margin-bottom: 70px;

            @include media('tablet', true) {
                margin-bottom: 45px;
            }
        }

        &__swiper-container {
            cursor: url('../img/cursor-slider.png') 62 62, auto;
            margin-right: -70px;
            overflow: hidden;

            @include media('tablet-1200', true) {
                margin-right: -45px;
            }

            &:hover {
                cursor: url('../img/cursor-slider.png') 62 62, auto;

            }
        }

        &__swiper-wrapper {
            &:hover {
                cursor: url('../../img/cursor-slider.png') 62 62, auto;
            }
        }

        &__swiper-slide {}

        &__card {
            height: 630px;
            position: relative;

            @include media('tablet-1000', true) {
                height: 550px;
            }
        }

        &__img {}

        &__text {
            position: absolute;
            left: 30px;
            bottom: 20px;
            text-transform: none;

        }

        &__swiper-button-prev {}

        &__swiper-button-next {}
    }

    .swiper-container2 {}

    .swiper-wrapper {}

    .swiper-slide {
        width: auto;
    }

    .swiper-button-prev2 {
        margin-right: 10px;
    }

    .swiper-button-next2 {}

    .swiper-button-wrapper {
        display: flex;
        position: absolute;
        right: 0;
        top: 0;
    }